
import { defineComponent, ref, toRefs, watch } from 'vue';

export default defineComponent({
  name: 'CCInput',
  props: {
    value: {
      type: String,
      default: '',
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    maxlength: {
      type: Number,
      default: -1,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    /**
     * input的值是否为目录
     */
    directory: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: '',
    },
  },
  emits: ['update:value', 'change', 'input', 'key-up', 'key-down', 'key-enter'],
  setup(props, { emit }) {
    const focusColor = '#fd942b';
    const borderColor = ref('transparent');
    const text = ref(props.value || '');
    watch(
      () => props.value,
      (val) => {
        if (text.value !== val) {
          text.value = val;
          emit('change', text.value);
        }
      }
    );
    const hover = ref(false);
    const elInput = ref<HTMLInputElement>();
    return {
      elInput,
      hover,
      text,
      borderColor,
      getCSS() {
        const css: string[] = [];
        if (props.directory) {
          css.push(`cursor: pointer !important`);
        }
        return css.join(';');
      },
      onKeyDown(event: KeyboardEvent) {
        if (event.key === 'ArrowUp') {
          event.preventDefault();
          emit('key-up');
        } else if (event.key === 'ArrowDown') {
          event.preventDefault();
          emit('key-down');
        } else if (event.key === 'Enter') {
          event.preventDefault();
          emit('key-enter');
        }
      },
      onInput(event: InputEvent) {
        emit('update:value', text.value);
        emit('input', text.value);
      },
      onFocusin(event: FocusEvent) {
        borderColor.value = focusColor;
        (event.target as HTMLInputElement).select();
      },
      onClean() {
        if (text.value === '') {
          return;
        }
        text.value = '';
        emit('update:value', text.value);
        emit('change', text.value);
      },
      onFocusout() {
        borderColor.value = 'transparent';
      },
      onBlur() {
        emit('update:value', text.value);
        emit('change', text.value);
      },
      /**获得焦点，并选中指定区域的文本，区间无效则选中全部 */
      doFocus(range1: number = -1, range2: number = -1) {
        if (elInput.value) {
          elInput.value.focus();
          const len = elInput.value.value.length;
          if (range1 >= 0 && range2 <= len && range1 < range2) {
            elInput.value.setSelectionRange(range1, range2);
          }
        }
      },
    };
  },
});
